import React, { useState } from 'react';
import styled from 'styled-components';
import {
  FormControl,
  Stack,
  Input,
  Flex,
  Checkbox,
  Spacer,
  Button,
  useToast,
  FormLabel,
} from '@chakra-ui/react';
import { useDispatch } from 'react-redux';
import { setProjectList } from '../features/projectsSlice';

const ProjectInput = () => {
  const [title, setTitle] = useState('');
  const [hidden, setHidden] = useState(false);
  const [featured, setFeatured] = useState(false);
  const [currId, setId] = useState(0);
  const toast = useToast();
  const dispatch = useDispatch();

  const createProject = () => {
    toast({
      title: 'Project Created!',
      description: 'A new project has been created!',
      status: 'success',
      duration: 3000,
      isClosable: true,
    });

    dispatch(
      setProjectList({
        id: currId,
        name: title,
        featured: featured,
        project: 'programming',
        hidden: hidden,
      })
    );
    setId(currId + 1);
  };

  return (
    <InputWrapper>
      <FormControl>
        <Stack mb={'5'} direction="column">
          <FormLabel mr={'auto'}> Title </FormLabel>
          <Input
            type="text"
            onBlur={event => setTitle(event.currentTarget.value)}
          />
          <Flex>
            <Checkbox onChange={() => setHidden(!hidden)}> Hidden </Checkbox>
            <Checkbox ml={4} onChange={() => setFeatured(!featured)}>
              {' '}
              Featured{' '}
            </Checkbox>
            <Spacer />
            <Button colorScheme="yellow" onClick={createProject}>
              {' '}
              Add Project!
            </Button>
          </Flex>
        </Stack>
      </FormControl>
    </InputWrapper>
  );
};

const InputWrapper = styled.div`
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding: 20px;
  margin-bottom: 20px;
`;
export default ProjectInput;
