import React, { useLayoutEffect } from 'react';
import styled from 'styled-components';
import Carousel from './Carousel';
import PortfolioButtons from './PortfolioButtons';
import { motion } from 'framer-motion';
import StarsIcon from '@material-ui/icons/Stars';

const Content = () => {
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  });

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <Container>
        <Carousel />

        <Subtitle>
          <StarsIcon />
          <span>Highlighted Projects </span>
          <StarsIcon />
        </Subtitle>
        <PortfolioButtons />
      </Container>
    </motion.div>
  );
};

const Container = styled.div`
  min-height: 100vh;
  position: relative;
  z-index: 1;
  overflow-x: hidden;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    background-image: url('./imgs/bg.jpeg');
    opacity: 40%;
  }
`;
const Subtitle = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
  span {
    margin: 0 20px;
  }
`;
export default Content;
